import React, { useEffect, useState } from "react";
import Card from "@mui/material/Card";
import { CardContent, Grid, Typography, CardActionArea } from "@mui/material";
import { BounceLoader } from "react-spinners";

const Home = () => {
  const [modelData, setModelData] = useState({});
  useEffect(() => {
    const data = require("../../data/data.json");
    setModelData(data);
  }, []);

  const AppCard = ({ children }) => (
    <Grid item xs={12} md={6} xl={4}>
      <Card>
        <CardActionArea>
          <CardContent>{children}</CardContent>
        </CardActionArea>
      </Card>
    </Grid>
  );

  const TitleCard = ({ title, children }) => (
    <AppCard>
      <Typography variant="h6" color="text.secondary" gutterBottom>
        {title}
      </Typography>
      {children}
    </AppCard>
  );

  const Score = ({ children }) => <Typography variant="h2" align="center">
    {children}
  </Typography>

  return (
    <Grid container spacing={2} mt={2} mx={4} maxWidth="80%">
      <TitleCard title="Precis√£o">
        <Score>
          {modelData && modelData.accuracy + "%"}
          {!modelData && <BounceLoader />}
        </Score>
      </TitleCard>
      <TitleCard title="Tamanho do dataset de treino">
        <Score>
          {modelData && modelData.noTrainingDataset + " tweets"}
          {!modelData && <BounceLoader />}
        </Score>
      </TitleCard>
      <TitleCard title="Tamanho do dataset de teste">
        <Score>
          {modelData && modelData.noTestingDataset + " tweets"}
          {!modelData && <BounceLoader />}
        </Score>
      </TitleCard>
      <TitleCard title="Qtd. Positivos">
        <Score>
          {modelData && modelData.noPositives + " tweets"}
          {!modelData && <BounceLoader />}
        </Score>
      </TitleCard>
      <TitleCard title="Qtd. Neutros">
        <Score>
          {modelData && modelData.noNeutrals + " tweets"}
          {!modelData && <BounceLoader />}
        </Score>
      </TitleCard>
      <TitleCard title="Qtd. Negativos">
        <Score>
          {modelData && modelData.noNegatives  + " tweets"}
          {!modelData && <BounceLoader />}
        </Score>
      </TitleCard>
    </Grid>
  );
};

export default Home;
