import {
  getCollapseBtn,
  getDrawerSidebar,
  getSidebarContent,
} from "@mui-treasury/layout";
import styled from "styled-components";
import SidebarItem from "../SidebarItem";
import SidebarExpandedItem from "../SidebarExpandedItem";
import HomeIcon from "@material-ui/icons/Home";
import WbCloudyRoundedIcon from "@material-ui/icons/WbCloudyRounded";
import { List, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";

const DrawerSidebar = getDrawerSidebar(styled);
const SidebarContent = getSidebarContent(styled);
const CollapseBtn = getCollapseBtn(styled);

const routes = [
  { to: "/", icon: <HomeIcon />, name: "In√≠cio"},
  { to: "/cloud", icon: <WbCloudyRoundedIcon />, name: "Word Cloud"}
];

const Sidebar = ({ sidebarState }) => (
  <DrawerSidebar sidebarId={"main"}>
    <SidebarContent style={{ textAlign: "center", marginTop: 20 }}>
      {(sidebarState.open || sidebarState.collapsed) && (
        <>
          {routes.map(item => <SidebarItem to={item.to} icon={item.icon} /> )}
        </>
      )}
      {(sidebarState.open || !sidebarState.collapsed) && (
        <List>
          {routes.map(item => <SidebarExpandedItem to={item.to} icon={item.icon} title={item.name} />)}
        </List>
      )}
    </SidebarContent>
    <CollapseBtn />
  </DrawerSidebar>
);

export default Sidebar;
